
.container {
 margin: 2rem auto;
 width: 800px;
 max-width: 80%;
 background-color: #DDD;  
 border-radius: 8px;
 box-shadow: -3px 13px 26px -6px rgba(139,151,227,1);
}

.main-content__update{
    text-align: center;
    padding: 10px;
    background-color: #79C879;
    color: white;
    margin-bottom: 10px;
    display: block;
    border-radius: 8px;
  }

  .nav{
      display: flex;
      justify-content: space-between;
      align-items: center;
  }

  .nav__logo{
    margin-left: 1rem;;
  }

  .nav-list{
      display: flex;
  }

   .nav-list__home, .nav-list__posts{
    border-radius: 8px;
    border-style: solid;
    border-width: 2px;
    list-style-type: none;
    margin: 1rem;
    padding: .5rem;
  }

  .nav-list__home:hover, .nav-list__posts:hover{
    background-color: #000;
    color: #FFF;
    transition: 1s;
  }

  .add-post, .update-post{
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .add-post__postTitle, .add-post__postBody, .update-post__postTitle, .update-post__postBody{
    border-radius: 6px;
    padding: .5rem;
    margin: 1rem;
    background:#c0d2fa;
    color:#fff;
  }

  .main-content__posts{
    text-align: center;
    margin: 2rem;
    background-color: #79C879;
    width: 50%;
    border-radius: 6px;
  }

